import * as React from 'react';
import { IconButton } from './Basics';
import type { IIconButtonProps } from './Basics';
import { IconUndo } from '../icons';

export function UndoButton(props: IIconButtonProps) {
  const {
    icon = <IconUndo />,
    ...rest
  } = props;

  return (
    <IconButton
      testid="undo_button"
      icon={icon}
      {...rest}
    />
  )
}